import { useMemo } from "react"
import { Link } from "react-router-dom"
import { useChats } from "@rp/react/tanstack-api/chat"
import { Box, Divider, Skeleton, Typography } from "@rp/react/web/mui/core"
import { CHATS_ROUTE, CHAT_ID_ROUTE } from "@rp/react/web/router"
import { MobileArrowForwardButton } from "../MobileArrowForwardButton"

export const MobileRecentChats = () => {
  const { data, isLoading } = useChats()

  const recent = useMemo(() => {
    return (
      data
        ?.sort(
          (first, second) =>
            new Date(second.lastUpdated).getTime() - new Date(first.lastUpdated).getTime(),
        )
        .slice(0, 3) ?? []
    )
  }, [data])

  return (
    <Box sx={{ mt: 1, px: 2 }}>
      {isLoading && (
        <Box sx={{ height: 165, mt: 3, "& .MuiSkeleton-root": { my: 1 } }}>
          <Divider sx={{ mb: 3 }} />
          <Skeleton width="50%" height={30} />
          <Skeleton width="100%" height={30} />
          <Skeleton width="100%" height={30} />
          <Skeleton width="100%" height={30} />
        </Box>
      )}
      {data && data.length > 0 && (
        <>
          <Divider sx={{ my: 3 }} />
          <MobileArrowForwardButton label="Recent" to={CHATS_ROUTE} sx={{ px: 0 }} />
          <Box sx={{ display: "flex", flexDirection: "column", gap: 0.5, mt: 1.5 }}>
            {recent.map(chat => (
              <Typography
                component={Link}
                key={chat.id}
                to={CHAT_ID_ROUTE.replace(":id", chat.id)}
                sx={{
                  color: "inherit",
                  textDecoration: "none",
                  textTransform: "none",
                  lineHeight: "30px",
                  overflow: "hidden",
                  textOverflow: "ellipsis",
                  whiteSpace: "nowrap",
                }}
              >
                {chat.name}
              </Typography>
            ))}
          </Box>
        </>
      )}
    </Box>
  )
}
